import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';
import HomeIcon from '@material-ui/icons/Home';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import GrainIcon from '@material-ui/icons/Grain';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    link: {
      display: 'flex',
      cursor: 'pointer'
    },
    icon: {
      marginRight: theme.spacing(0.5),
      width: 20,
      height: 20,
    },
    breadcrumbs : {
      marginTop: '30px',
marginLeft : '45px',
display: 'none',
    }
  }),
);


export default function IconBreadcrumbs(props:any) {

  const classes = useStyles();
   const pressHandler = (num:any) => props.clicked(num);
  return (
    <Breadcrumbs aria-label="breadcrumb" className={classes.breadcrumbs}>
      <Link color="inherit" onClick={()=>(pressHandler(0))}  className={classes.link}>
        <HomeIcon className={classes.icon} />
        Type King
      </Link>
      <Link
        color="inherit"
        onClick={()=>(pressHandler(1))}
        className={classes.link}
      >
        <WhatshotIcon className={classes.icon} />
        Weather Grabbr
      </Link>
      <Link
        color="inherit"
        onClick={()=>(pressHandler(2))}
        className={classes.link}
      >
        <GrainIcon className={classes.icon} />
        Todoz
      </Link>
      <Link
        color="inherit"
        onClick={()=>(pressHandler(3))}
        className={classes.link}
      >
        <WhatshotIcon className={classes.icon} />
        Ez Sens
      </Link>
    </Breadcrumbs>
  );
}
